.button {
  margin-top: 1em;
  font-family: 'Nunito', sans-serif;
  font-size: 22px;
  text-transform: uppercase;
  letter-spacing: 1.3px;
  font-weight: 700;
  color: #fff;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-shadow: 0 0 0.15em #e9b5d2;
  background: #fc60a3;
background: linear-gradient(90deg, rgba(252,96,163,1) 0%, rgb(245, 129, 179) 100%);
  border: none;
  border-radius: 1000px;
  box-shadow: 12px 12px 24px rgba(233, 77, 207, 0.64);
  transition: all 0.3s ease-in-out 0s;
  cursor: pointer;
  outline: none;
  position: relative;
  padding: 10px;
  z-index: 100;
  width: 40%;
  }

.button::before {
/* content: ''; */
  border-radius: 1000px;
  /* min-width: calc(200px); */
  /* min-height: calc(60px); */
  border: 6px solid #fc60a3;
  box-shadow: 0 0 60px rgba(255, 0, 255, 0.64);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all .3s ease-in-out 0s;
}

.button:hover, .button:focus {
  color: #313133;
  transform: translateY(-6px);
}

.button:hover::before, .button:focus::before {
  opacity: 1;
}

.button::after {
  /* content: ''; */
  width: 30px; height: 30px;
  border-radius: 100%;
  border: 6px solid #fc60a3;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* animation: ring 3s infinite; */
}

.button:hover::after, .button:focus::after {
  animation: none;
  display: none;
}

@keyframes ring {
  0% {
    width: 30px;
    height: 30px;
    opacity: 1;
  }
  100% {
    width: 200px;
    height: 200px;
    opacity: 0;
  }
}
